* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  align-items: center;
}

.search-form {
  z-index: 1;
  text-align: center;
  justify-content: center;
  gap: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

input {
  width: 400px;
  color: #000;
  background: #fff;
  border: none;
  border-radius: 9999px;
  outline: none;
  padding: 10px 20px;
  font-size: 16px;
  box-shadow: inset 2px 5px 10px rgba(5, 5, 5, .435);
}

.btn-search {
  cursor: pointer;
  max-width: 190px;
  color: #000;
  background: #fff;
  border: none;
  border-radius: 9999px;
  outline: none;
  padding: 10px 20px;
  font-size: 16px;
  transition: all .2s ease-in-out;
  box-shadow: inset 2px 5px 10px rgba(5, 5, 5, .435);
}

.btn-search:hover {
  background-color: #f2f2f2;
  box-shadow: inset 2px 5px 10px rgba(0, 0, 0, .63);
}

.btn-search:active {
  transform: scale(.95);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding-top: 20px;
  display: flex;
}

.photo-card {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .4), 0 7px 13px -3px rgba(0, 0, 0, .3), inset 0 -3px rgba(0, 0, 0, .2);
}

.photo-card:hover {
  scale: 1.01;
}

img {
  width: 500px;
  height: 300px;
}

.info {
  gap: 10px;
  padding-bottom: 5px;
  padding-left: 10px;
  font-size: 15px;
  display: flex;
}

.load-more {
  cursor: pointer;
  width: 200px;
  color: #fff;
  background: #000;
  border: none;
  border-radius: 9999px;
  outline: none;
  justify-content: center;
  margin-top: 40px;
  padding: 10px 20px;
  font-size: 16px;
  transition: all .2s ease-in-out;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: inset 2px 5px 10px rgba(5, 5, 5, .435);
}

.load-more:hover {
  background-color: #1b1b1b;
  box-shadow: inset 2px 5px 10px rgba(53, 53, 53, .63);
}

